.header {
  align-items: flex-end;
}

.header h1 {
  flex-grow: 1;
}

.header h2 {
  font-size: 1.2rem;
  color: gray;
}
.search-input {
  width: auto;
  flex-grow: 1;
  margin-right: 3px;
}
.todo-list-item {
  font-size: 1.25rem;
}

.todo-list-item button {
  width: 35px;
  margin: 3px;
}

.todo-list-item-text {
  margin-left: 1.25rem;
  line-height: 35px;
  cursor: pointer;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.todo-list-item.done .todo-list-item-text {
  text-decoration: line-through;
}

.todo-list-item.priority .todo-list-item-text {
  font-weight: bold;
  color: royalblue;
}
.todo-list .list-group-item {
  padding: .25rem .75rem;
}
.task-add-form {
  margin-top: 10px;
}

.task-add-form input {
  margin-right: 3px;
  flex: 1 1;
}
.todo-app {
  margin: 2rem auto 0 auto;
  max-width: 500px;
}

.top-panel {
  margin: 1rem 0;
}
